@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
 font-family:'Roboto', sans-serif;
}
h1,h2,h3 {
  margin: 0.5rem 0;
}
h1{
  font-size: 1.5rem;
  color:rgb(206, 97, 19);
  font-weight: bold;
}
h2{
  font-size: 1.5rem;
  color: #404040;
  font-weight: bold;
}
h3{
  font-size: 1rem;
  color: #404040;
}
a{
  text-decoration: none;
  color: beige;
  margin-left:0.5rem 0;
}
.row{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.col-1{
  -webkit-flex:1 1;
          flex:1 1; 
}
.col-2{
  -webkit-flex:2 1;
          flex:2 1;
}
.block{
  background-color:rgb(235, 190, 76);
  padding: 1rem;
  margin: 0.5rem;
  border-radius: 0.5rem;
}
.row.center{
  -webkit-align-items: center;
          align-items: center;
}
img.photos{
  max-height: 8rem;
}
.photos{
  border-radius: 0.5rem;
}
button{
  font-size: 0.8rem;
  padding: 0.2rem;
  margin: 0.1rem;
  border-radius: 0.5rem;
  border: 0.1rem #404040 solid;
  background-color:rgb(206, 97, 19);
  width: 100%;
  cursor: pointer;
  color: beige;
}
button.add{
  background-color: rgb(251, 255, 0);
  width: 1.5rem;
  border-radius: 0.5rem;
  color: rgba(0, 0, 0, 0.993);
}
button.remove{
  background-color: rgb(255, 102, 0);
  width: 1.5rem;
  border-radius: 0.5rem;
}
button.badge{
  background-color: coral;
  border:none;
  color: aqua;
  width: 1.5rem;
}
tex.right{
  text-align: right;
}
.price{
  color: rgb(4, 66, 66);
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

